.row {
	cursor: pointer;
	height: 100%;
	display: flex;
	align-items: center;
	
	&:hover {
		background-color: #f3f3f6;
	}
	padding: 0 4px;
	padding-left: 12px;
}

.active {
	background: #ebebf0;
	&:hover {
		background-color: #ebebf0;
	}
}
.inputContainer {
	width: 190px;
	padding: 0 4px;
	margin-bottom: 10px;
	padding-left: 12px;
	padding-right: 12px;
}
// .input {
// 	width: 100%;
// }
.input { 
	width: 100%;
	padding: 2px 0;
}

.cursorInput {
	position: relative;
	width: 213px;
	display: flex;
	&:hover {
		.clearIcon {
			display: block;
		 }
	}
	.clearIcon {
		display: none;
		position: absolute;
		right: 4px;
		top: 7px;
		svg {
			cursor: pointer;
			fill: #b0b7c3;
			&:hover {
				fill: #00b831;
			}
		}
	}
}
.noShow {
	display: none;
}
.overlayClass {
	padding: 12px 0 0;
}