.input {
    border: none;
    outline: none;
    height: 100%;
    flex-basis: 0;
    flex-grow: 1;
    width: 0;
}

// 想移入元素就显示，必须保留位置， flex-grow:1 flex-base 0 width:0

.container {
    width: 175px;
    background-color: #fff;
   
    border-radius: 2px;
    padding: 2px 8px;
    transition: border-color .2s ease-in-out;
    display: flex;
    align-items: center;
    height: 24px;
    border-radius: 2px;
    &:hover,
    &.focused {
        border-color: #00b831;
    }
    &:hover {
        .clearIcon  {
            display: inline-block;
        }
        .show {
            display: inline-block;
        }
    }
}
.active {
    border-color: #00b831;
}

.clearIcon {
    fill: #b0b7c3;
    cursor: pointer;
    display: none;
    &:hover {
        fill:#00b831 ;
    }
}

.disabled {
    cursor: not-allowed;
    background-color: #f0f0f5;
    border-color: #d7d9db;;
    .input {
        cursor: not-allowed;
    }
}

.border {
    border: 1px solid #d7d9db;
}

.banBorder {
    border-bottom: 1px solid #d7d9db;
}
.parent {
    position: relative;
    .error {
        position: absolute;
        margin: 0;
        font-size: 12px;
        color: #ff534e;
        bottom: -20px;
    }
}

.toggleIcon {
    cursor: pointer;
    svg {
        fill: #b0b7c3;
        &:hover {
            fill: #00b831;
        }
    }
}
.onShow {
    display: none;
}